<template>
  <div id="terminal">
    <div class="terminal-wrap">
      <div class=fakeMenu>
        <div class="fakeButtons fakeClose"></div>
        <div class="fakeButtons fakeMinimize"></div>
        <div class="fakeButtons fakeZoom"></div>
      </div>
      <div class="fakeScreen">
        <p class="line">$ John-Lewis-Sims: ~ {{ currentFolder }} <input v-model="inputValue" v-on:keyup.enter="enter"></p>
        <div class="output">
          <transition name="fade">
            <div class="ls"  v-if="setInput == 'ls'">
              <p v-for="folder in folders" :key="folder">{{folder}}</p>
            </div>
            <div class="error"  v-if="setInput == 'error'">
              <p>command not found. try typing 'help' to see commands.</p>
            </div>
            <div class="help"  v-if="setInput == 'help'">
              <p>Commands: <span> ls - List All Sections and Projects </span> <span>cd - Jump to Section or Project</span>  <span>clear - Clear the Terminal</span></p>
            </div>
          </transition>
        </div>
      </div>

      <div class="links">
        <router-link to="/portfolio">Skip to Portfolio</router-link>
      </div>
    </div>



    <div class="folder-tabs">
      <div class="folder-tab career">
        <transition name="fade">
          <Career v-if="setInput == 'cd career'"/>
          <Skills v-if="setInput == 'cd skills'"/>
        </transition>
      </div>
    </div>

  </div>
</template>


<script>
  import Career from './Career.vue'
  import Skills from '@/components/Skills.vue'
  export default {
    components: {
      Career, Skills
    },
    data() {
      return {
        inputValue: '',
        setInput: '',
        currentFolder: 'johnsims$',
        // folders: ['career', 'projects', 'college-software', 'wordpress-plugins', 'towing-dispatch-app', 'video-management-app']
        folders: ['career', 'skills']
      }
    },
    methods: {
      enter() {
        this.setInput = this.inputValue
        switch (this.setInput) {
          case 'ls':
            break;
          case 'cd':
            break;
          case 'clear':
            this.setInput = ""
            break;
          case 'help':
            this.setInput = "help"
            break;
          case 'cd career':
            this.setInput = "cd career"
            break;
          case 'cd skills':
            this.setInput = "cd skills"
            break;
          default:
            this.setInput = "error"
        }
      }
    }
  }
</script>


